    },
    setListHeight() {
      if (this.$refs.list) {
        console.log("this.listHeight", this.listHeight);
        console.log("window.innerHeight", window.innerHeight);
        console.log(
          "this.$refs.list.getBoundingClientRect().top",
          this.$refs.list.getBoundingClientRect().top
        );
        this.listHeight =
          window.innerHeight - this.$refs.list.getBoundingClientRect().top - 10;
      }
      return { height: this.listHeight + "px" };
    },
  },
  mounted() {
    this.setListHeight();
  },
};
</script>

